import $ from 'jquery';
import 'foundation-sites/js/foundation/foundation';
import 'foundation-sites/js/foundation/foundation.dropdown';
import utils from '@bigcommerce/stencil-utils';

export const CartPreviewEvents = {
    close: 'closed.fndtn.dropdown',
    open: 'opened.fndtn.dropdown',
};

export default function (secureBaseUrl) {
    const loadingClass = 'is-loading';
    const $cart = $('[data-cart-preview]');
    const $cartDropdown = $cart.next();
    const $cartLoading = $('<div class="loadingOverlay"></div>');

    const $body = $('body');

    $body.on('cart-quantity-update', (event, quantity) => {
        if ($(window).width() > 1024) {
            $('.cart-quantity').text('(' + quantity + ')').toggleClass('countPill--positive', quantity > 0);
        }else{
            $('.cart-quantity').text(quantity).toggleClass('countPill--positive', quantity > 0);
        }
    });

     $cart.on('click', function(event) {
        if ($(window).width() > 1024) {
            event.preventDefault();
            const options = {
                template: 'common/cart-preview',
            };

            // Redirect to full cart page
            //
            // https://developer.mozilla.org/en-US/docs/Browser_detection_using_the_user_agent
            // In summary, we recommend looking for the string 'Mobi' anywhere in the User Agent to detect a mobile device.
            if (/Mobi/i.test(navigator.userAgent)) {
                return event.stopPropagation();
            }
           
            $(this).parent().toggleClass('is-open');

            $cartDropdown
                .addClass(loadingClass)
                .html($cartLoading);
            $cartLoading
                .show();

            utils.api.cart.getContent(options, (err, response) => {
                $cartDropdown
                    .removeClass(loadingClass)
                    .html(response);
                $cartLoading
                    .hide();
            });
        }
    });

    let quantity = 0;

    // Get existing quantity from localStorage if found
    if (utils.tools.storage.localStorageAvailable()) {
        if (localStorage.getItem('cart-quantity')) {
            quantity = Number(localStorage.getItem('cart-quantity'));
            $body.trigger('cart-quantity-update', quantity);
        }
    }

    // Get updated cart quantity from the Cart API
    const cartQtyPromise = new Promise((resolve, reject) => {
        utils.api.cart.getCartQuantity({ baseUrl: secureBaseUrl }, (err, qty) => {
            if (err) {
                reject(err);
            }
            resolve(qty);
        });
    });

    // If the Cart API gives us a different quantity number, update it
    cartQtyPromise.then(qty => {
        $body.trigger('cart-quantity-update', qty);
        if (utils.tools.storage.localStorageAvailable()) {
            localStorage.setItem('cart-quantity', qty);
        }
    });

     $(document).on('click', function(ev) {
        if ($(ev.target).closest('.navUser-item--cart').length === 0) {
            $cart.parent().removeClass('is-open');
        }
    });
}
